export const generateColumnSet = (formHeadSets, module = 'word', minWidth = 120) => {
  const INIT_PROP = {
    align: 'center',
    width: module === 'input' ? minWidth : '',
    minWidth: '',
    fixed: false,
    sortable: false,
    resizable: true,
    formatter: null,
    headerAlign: 'center'
  }

  return formHeadSets.map(item => {
    if (item.children) {
      for (let i = 0; i < item.children.length; i++) {
        item.children[i] = {
          module,
          prop: item.children[i].prop,
          label: item.children[i].label,
          ...INIT_PROP
        }
      }
      return {
        module,
        prop: item.prop,
        label: item.label,
        ...INIT_PROP,
        children: item.children
      }
    } else {
      return {
        module,
        prop: item.prop,
        label: item.label,
        ...INIT_PROP
      }
    }
  })
}
