<template>
  <div class="app-container">
    <!-- 头部搜索 -->
    <div class="clearfix top-filter">
      <el-form ref="filterItem" :model="query" inline>
        <el-form-item prop="startMonth" class="fl">
          <el-date-picker
            v-model="query.startMonth"
            type="month"
            style="width: 178px;"
            clearable
            value-format="yyyy-MM"
            format="yyyy年M月"
            placeholder="请选择开始月份"
          />
        </el-form-item>
        <el-form-item prop="endMonth" class="fl">
          <el-date-picker
            v-model="query.endMonth"
            type="month"
            style="width: 178px;"
            clearable
            value-format="yyyy-MM"
            format="yyyy年M月"
            placeholder="请选择结束月份"
          />
        </el-form-item>
        <!-- <el-form-item prop="unitName" class="fl">
          <el-input v-model.trim="query.materialCodeOrName" clearable placeholder="编号" class="filter-item" style="width: 178px;" />
        </el-form-item> -->
        <el-button
          class="filter-item"
          type="success"
          icon="el-icon-search"
          @click="init"
        >搜索</el-button>
        <el-button
          class="filter-item"
          type="warning"
          icon="el-icon-refresh-left"
          @click="resetForm('filterItem')"
        >重置</el-button>
        <!-- <el-button
          class="filter-item"
          type="primary"
          icon="el-icon-plus"
          @click="handleExport"
        >导出</el-button> -->
        <el-row>
          <!-- <el-button
            class="filter-item"
            type="info"
            icon="el-icon-setting"
            @click="setTable"
          >设置</el-button> -->
          <el-button
            class="filter-item"
            type="info"
            icon="el-icon-setting"
            @click="dialogVisible = true"
          >自定义统计</el-button>
          <el-button
            class="filter-item"
            type="primary"
            icon="el-icon-plus"
            @click="handleExport"
          >导出</el-button>
        </el-row>
      </el-form>
    </div>

    <!-- 图表折叠面板 -->
    <!-- <el-collapse style="margin: 0 0 20px 0;" @change="$refs.chartCom.reset()">
      <el-collapse-item title="图表展示" name="1">
        <chart-com ref="chartCom" style="height: 310px;" />
      </el-collapse-item>
    </el-collapse> -->

    <!-- 列表 -->
    <el-row>
      <table-com
        :ref="refTableName"
        :loading="loading"
        :ref-name="refName"
        :index="true"
        :selection="false"
        :table-column-sort="true"
        :form-ident="formIdent"
        :table-data="tableData"
        :column-list="columnList"
        :show-operate="false"
        :refresh-key="refreshKey"
        :expand-list="expandList"
        @rowClick="handleRowClick"
        @refresh="searchInit"
      />
    </el-row>
    <pagination :page.sync="pageInfo.current" :limit.sync="pageInfo.size" :total="total" :page-sizes="[50, 100, 150, 500]" @pagination="init" />

    <!-- 编辑弹窗 -->
    <edit-dialog
      v-model="dialogVisible"
      :form-ident="formIdent"
      :table-data="tableData"
      :column-list="columnListInput"
      :expand-list="expandList"
      @handleSubmitCustomData="handleSubmitCustomData"
    />

  </div>
</template>

<script>
import tableHeadSet from '@/mixins/tableHeadSet'
// import ChartCom from './Charts'
import paginationPage from '@/mixins/paginationPage'
import EditDialog from './Dialog'
import { getListApi } from '@/apis/modules/analysis/warehouseUtilizationRate'
import { generateColumnSet } from './column'
import moment from 'moment'
moment.locale('zh-cn')
import { _debounce } from '@/utils'
import * as XLSX from 'xlsx'
import { saveAs } from 'file-saver'

const INIT_COLUMN = [
  {
    module: 'word',
    prop: 'warehouseCode',
    label: '仓库编号',
    align: 'center',
    // width: 120,
    // minWidth: 160,
    fixed: false,
    sortable: false,
    resizable: true,
    formatter: null,
    headerAlign: 'center'
  },
  {
    module: 'word',
    prop: 'warehousePlaceDescription',
    label: '仓库描述',
    align: 'center',
    minWidth: '200',
    fixed: false,
    sortable: false,
    resizable: true,
    formatter: null,
    headerAlign: 'center'
  },
  {
    module: 'word',
    prop: 'warehouseGeographicPosition',
    label: '仓库位置',
    align: 'center',
    minWidth: '',
    fixed: false,
    sortable: false,
    resizable: true,
    formatter: null,
    headerAlign: 'center'
  }
]

export default {
  // name: 'AnalysisWarehouseUtilizationRate',
  components: {
    // ChartCom,
    EditDialog
  },
  mixins: [tableHeadSet, paginationPage],
  data() {
    return {
      title: this.$route.meta.title,
      active: '1',
      refTableName: 'baseTableRef',
      refName: 'table',
      loading: false,
      query: {
        startMonth: '',
        endMonth: ''
      },
      tableData: [],
      columnList: [

        // {
        //   module: 'word',
        //   prop: 'totalAmount',
        //   label: '库位数',
        //   align: 'center',
        //   minWidth: '',
        //   fixed: false,
        //   sortable: false,
        //   resizable: true,
        //   formatter: null,
        //   headerAlign: 'center'
        // },
        // {
        //   module: 'word',
        //   prop: 'inTransitNum',
        //   label: '1月实际占用',
        //   align: 'center',
        //   minWidth: '',
        //   fixed: false,
        //   sortable: false,
        //   resizable: true,
        //   formatter: null,
        //   headerAlign: 'center'
        // },
        // {
        //   module: 'word',
        //   prop: 'materialGroupCode',
        //   label: '1月占用率',
        //   align: 'center',
        //   minWidth: '',
        //   fixed: false,
        //   sortable: false,
        //   resizable: true,
        //   formatter: null,
        //   headerAlign: 'center',
        //   headerTip: '该月实际占用库位'
        // },
        // {
        //   module: 'word',
        //   prop: 'assessType_dictText',
        //   label: '2月实际占用',
        //   align: 'center',
        //   minWidth: '',
        //   fixed: false,
        //   sortable: false,
        //   resizable: true,
        //   formatter: null,
        //   headerAlign: 'center'
        // },

        // {
        //   module: 'word',
        //   prop: 'assessType_dictText1',
        //   label: '2月实际占用率',
        //   align: 'center',
        //   minWidth: '',
        //   fixed: false,
        //   sortable: false,
        //   resizable: true,
        //   formatter: null,
        //   headerAlign: 'center'
        // }
      ],
      columnMonth: ['january', 'february', 'march', 'april', 'may', 'june', 'july', 'august', 'september', 'october', 'november', 'december'],
      // columnSets: generateColumnSet(this.columnTitle),
      columnSets: [
      ],
      columnSetsInput: [
      ],
      columnListInput: [],
      tableColumnSortData: [],
      dialogForm: {},
      supplierPullList: [],
      editType: 'add',
      dialogVisible: false
    }
  },
  computed: {
    dialogTitle() {
      if (this.editType === 'add') {
        return `新增${this.title}信息`
      } else {
        return `编辑${this.title}信息`
      }
    }
  },
  mounted() {
    // this.columnSets = [...this.columnSets, ...generateMonthColumnSet(this.columnMonth)]
    // this.columnSetsInput = [...this.columnSetsInput, ...generateMonthColumnSet(this.columnMonth, 'input')]
    // this.columnListInput = this.columnList.map(i => {
    //   return {
    //     ...i,
    //     module: 'input'
    //   }
    // })
    this.columnSets = [...INIT_COLUMN]
    this.initQueryTime()
    this.init()
  },
  methods: {
    initQueryTime() {
      // this.query.startDate = moment(new Date()).subtract(1, 'months').format('YYYY-MM-DD')
      // this.query.endDate = moment(new Date()).format('YYYY-MM-DD')
      this.query.startMonth = moment().subtract(12, 'months').format('yyyy-MM')
      this.query.endMonth = moment().format('yyyy-MM')
    },
    init(type) {
      console.log('init method')
      this.loading = true
      const params = type ? { ...this.query } : {
        ...this.query,
        page: {
          ...this.pageInfo
        }
      }
      return new Promise((resolve, reject) => {
        getListApi({
          ...params
        }).then(res => {
          const { code } = res
          if (!code) {
            const { formHeadSets, pageData } = res.data
            this.columnList = generateColumnSet(JSON.parse(JSON.stringify(formHeadSets)))
            this.columnListInput = generateColumnSet(JSON.parse(JSON.stringify(formHeadSets)), 'input', 160)
            // columnListInput
            const { list, ...pageInfo } = pageData
            this.initPage(pageInfo)
            this.tableData = list
            // const { formIdent, formHeadSets, pageList } = data
            // this.formIdent = formIdent
            // this.columnList = this.initTableHeadMixins(formHeadSets)
            // this.columnListInput = this.initTableHeadMixins(formHeadSets, 'input', 'columnSetsInput')
            // const pageData = pageList || []
            // this.tableData = pageData.map(i => {
            //   return { ...i.monthMap, ...i }
            // })
          }
          this.loading = false
          resolve(res)
        }).catch(err => {
          this.loading = false
          reject(err)
        })
      })
    },
    handleRowClick({ row, column, cell, event }) {
      console.log(row)
      if (column.label === '操作') {
        return false
      }
      this.rightObj = { ...row }
    },
    refresh() {
      // 更新分页
      this.init('clean')
    },
    /*
    * 用户修改完数据点击确定
    * 1. 提交所有数据给后台，后台生成echarts需要的数据
    * 2. 更新echarts
    */
    handleSubmitCustomData() {

    },
    /*
    *   导出excel
    *   1. 上传 echarts 的图片
    *   2. 调倒出接口
    */
    handleExport: _debounce(function() {
      const elTable = this.$refs[this.refTableName].$refs[this.refName]
      // 因为element-ui的表格的fixed属性导致多出一个table，会下载重复内容，这里删除掉
      if (elTable.$el.querySelector('.el-table__fixed')) {
        elTable.$el.removeChild(elTable.$el.querySelector('.el-table__fixed'))
      }
      if (elTable.$el.querySelector('.el-table__fixed-right')) {
        elTable.$el.removeChild(elTable.$el.querySelector('.el-table__fixed-right'))
      }
      const wb = XLSX.utils.table_to_book(elTable.$el)
      const wbout = XLSX.write(wb, { bookType: 'xlsx', bookSST: true, type: 'array' })
      try {
        // 导出Excel
        const time = new Date().toLocaleString().replace(/\//g, '').replace(/:/g, '').replace(' ', '')
        saveAs(new Blob([wbout], { type: 'application/octet-stream' }), `库位利用率分析_${time}.xlsx`)
        this.baseTableKey++ // 更新表格组件，防止操作列消失
      } catch (e) {
        if (typeof console !== 'undefined') console.error(e, wbout)
      }
      return wbout
    }, 800, false),
    setTable() {
      this.$refs[this.refTableName].sortDialogVisible = true
    },
    resetForm(formName) {
      this.$refs[formName].resetFields()
      this.searchInit()
    }
  }
}
</script>

<style lang="scss" scoped>

</style>
